* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{width:100%;height:100%}.combined-container{position:fixed;width:100%;height:100%;background-color:#ececec;z-index:-100}

.navbar{width:100vw;height:8vh;background-color:#ececec;position:fixed;top:0;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;box-shadow:0px 4px 37px -14px #2d3032;font-family:"Montserrat",sans-serif;font-weight:700}.logo-container{height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-left:1.5rem}.logo-container .logo{max-width:auto;max-height:90%}.navbar-links{height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.navbar-links ul{display:-webkit-flex;display:flex;height:100%;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;list-style:none}.navbar-links ul li{height:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:0 2rem}.navbar-links ul li a{text-decoration:none;color:#2d3032;font-size:1.2rem}.navbar-links ul li a:hover{color:#7611ac}.navbar-links ul li .resume-btn{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:2px solid #7611ac;width:130px;height:50px;border-radius:7px;color:#7611ac}.navbar-links ul li .resume-btn:hover{background-color:rgba(118,17,172,0.19)}.toggle-navbar{height:100%;display:none;-webkit-align-items:center;align-items:center;position:absolute;right:35px}.toggle-navbar .bar,.toggle-navbar .bar::before,.toggle-navbar .bar::after{display:block;width:2rem;height:3px;background-color:#2d3032;border-radius:2px;position:relative;transition:all 0.5s ease-in-out}.toggle-navbar .bar::before,.toggle-navbar .bar::after{content:"";position:absolute}.toggle-navbar .bar::before{bottom:7px}.toggle-navbar .bar::after{top:7px}.toggle-navbar.active .bar{-webkit-transform:translateX(-32px);transform:translateX(-32px);background:transparent}.toggle-navbar.active .bar::before{-webkit-transform:translate(32px, 9px) rotate(45deg);transform:translate(32px, 9px) rotate(45deg)}.toggle-navbar.active .bar::after{-webkit-transform:translate(32px, -5px) rotate(-45deg);transform:translate(32px, -5px) rotate(-45deg)}@media (max-width: 750px){.navbar{-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start}.navbar-links{display:-webkit-flex;display:flex;-webkit-transform:scaleY(0);transform:scaleY(0);-webkit-transform-origin:top;transform-origin:top;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:100%;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;background-color:#ececec;box-shadow:0px 5px 15px -2px #2d3032;transition:all 0.4s ease-in-out}.navbar-links ul{margin:0;margin-bottom:10px;padding:0;width:100%;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.navbar-links ul li{-webkit-justify-content:center;justify-content:center;height:3rem}.navbar-links ul li a{opacity:0;transition:opacity 0.15s ease-in-out}.navbar-links.active{-webkit-transform:scaleY(1);transform:scaleY(1);-webkit-transform-origin:top;transform-origin:top}.navbar-links.active ul li a{opacity:1;transition:opacity 0.25s ease-in-out 0.3s}.toggle-navbar{display:-webkit-flex;display:flex}}

#Home{height:100vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1.5rem}#Home .content{width:Max(50rem, 50%);margin-left:3vw;line-height:0}#Home .content .greeting{color:#2d3032;font-family:"Montserrat",sans-serif;font-weight:800;font-size:1.5rem}#Home .content .name{color:#7611ac;font-family:"Montserrat",sans-serif;font-size:3.5rem;font-weight:700;line-height:1rem}#Home .content .intro{color:#2d3032;font-family:"Open Sans",sans-serif;font-size:2.5rem;font-weight:500;line-height:1rem}#Home .content .about{color:#2d3032;font-family:"Open Sans",sans-serif;font-size:1.5rem;font-weight:500;line-height:1em;margin-top:2rem}#Home .content hr{border:1px solid rgba(118,17,172,0.19);width:80%;margin-right:20%}#Home .content .logo{font-size:2rem;color:#2d3032;margin:1.5rem;margin-left:0}#Home .content .logo:hover{color:#7611ac}@media (max-width: 750px){#Home .content .greeting{color:#2d3032;font-family:"Montserrat",sans-serif;font-weight:600;font-size:1rem}#Home .content .name{font-size:1.8rem;font-weight:700}#Home .content .intro{font-size:1.5rem;font-weight:400}#Home .content .about{font-size:1rem;font-weight:300}#Home .content .logo{font-size:1.5rem}}

#Skills{min-height:100vh;display:-webkit-flex;display:flex;background-color:#ececec;padding:1.5rem}#Skills .content{width:Max(50rem, 50%);margin:0;margin-left:3vw;margin-top:4vh;line-height:0;overflow-wrap:break-word}#Skills .content>p{font-family:"Montserrat",sans-serif;font-size:2.2rem;font-weight:700;color:#7611ac;margin-bottom:1rem}#Skills .content hr{width:54%;border:1px solid rgba(118,17,172,0.19)}#Skills .content .languages{margin-top:5rem}#Skills .content .languages>p{color:#2d3032;font-family:"Montserrat",sans-serif;font-weight:600;font-size:1.5rem}#Skills .content .languages .language-logos{display:grid;grid-template-columns:repeat(auto-fit, MinMax(14rem, 1fr));grid-row-gap:4rem;row-gap:4rem;margin-top:3rem}#Skills .content .libraries{margin-top:2rem}#Skills .content .libraries>p{color:#2d3032;font-family:"Montserrat",sans-serif;font-weight:600;font-size:1.5rem}#Skills .content .libraries .libraries-logos{display:grid;grid-template-columns:repeat(auto-fit, MinMax(14rem, 1fr));grid-row-gap:4rem;row-gap:4rem;margin-top:3rem}#Skills .content .skill-logo{height:4rem}@media (max-width: 530px){#Skills .content{line-height:1.5rem}#Skills .content>p{font-size:1.4rem}#Skills .content hr{width:18rem}#Skills .content .languages{margin-top:2rem}#Skills .content .languages>p{font-size:1rem}#Skills .content .languages .language-logos{grid-template-columns:repeat(auto-fit, MinMax(8rem, 1fr));grid-row-gap:3rem;row-gap:3rem}#Skills .content .libraries>p{font-size:1rem}#Skills .content .libraries .libraries-logos{grid-template-columns:repeat(auto-fit, MinMax(8rem, 1fr));grid-row-gap:3rem;row-gap:3rem}#Skills .content .skill-logo{height:40px}}

